import request from '@/utils/request'
// import { duration } from 'moment'
const qq = function (data = {}, parmas = {}, url = '', method = 'get') {
  return request({
    url: url,
    method: method,
    data,
    parmas
  })
}
export const login = function (data = {}, parmas = {}) {
  return request({
    url: '/app/v1_0/authorizations',
    method: 'POST',
    data,
    parmas
  })
}
export const getcode = function (dat = { data: {}, parmas: {} }) {
  return qq(dat.data, dat.parmas, `/app/v1_0/sms/codes/${dat.mobile}`)
}
export const userinfo = function (data = {}, parmas = {}) {
  return request({
    url: '/app/v1_0/user',
    method: 'get',
    data,
    parmas
  })
}
export const resetpd = function (data) {
  return request({
    url: '/app/v1_0/user/channels',
    method: 'PUT',
    data
  })
}
export const userprofile = function (data) {
  return request({
    url: '/app/v1_0/user/profile',
    data,
    method: 'GET'
  })
}
export const imgchange = function (data) {
  return request({
    url: '/app/v1_0/user/photo',
    method: 'patch',
    data
  })
}
export const userchange = function (data) {
  return request({
    url: '/app/v1_0/user/profile',
    method: 'PATCH',
    data
  })
}
// 获取用户文章
export const usermyart = function (data) {
  return request({
    url: '/app/v1_0/user/articles',
    method: 'GET',
    params: data
  })
}
// 获取用户收藏列表
export const usermysc = function (data) {
  return request({
    url: '/app/v1_0/article/collections',
    method: 'GET',
    params: data
  })
}
// 获取用户阅读历史
export const usermyhis = function (data) {
  return request({
    url: '/app/v1_0/user/histories',
    method: 'GET',
    params: data
  })
}
