<template>
  <div>
    <div class="top">
      <div class="denglu" @click="$router.push('/login')" v-if="!$store.state.token">
        <van-image round width="66" height="66" :src="imglist.tu1" />
        <span>点击登录</span>
      </div>
      <div class="user" v-else>
        <div class="shang">
          <div class="zuo">
            <van-image round width="66" height="66" :src="user.photo" />
            <span>{{user.name}}</span>
          </div>
          <div>
            <van-button round type="info" size="mini" @click="$router.push('userinfo')">编辑资料</van-button>
          </div>
        </div>
        <div class="xia">
          <div class="item">
            <span>{{user.art_count}}</span>
            <span>头条</span>
          </div>
          <div class="item">
            <span>{{user.follow_count}}</span>
            <span>关注</span>
          </div>
          <div class="item">
            <span>{{user.fans_count}}</span>
            <span>粉丝</span>
          </div>
          <div class="item">
            <span>{{user.like_count}}</span>
            <span>获赞</span>
          </div>
        </div>
      </div>
    </div>
    <van-grid :column-num="3">
      <van-grid-item icon="photo-o" text="我的收藏" @click="$router.push('myartical/shoucang')">
        <i slot="icon" class="iconfont icon-shoucang" style="color:red"></i>
      </van-grid-item>
      <van-grid-item icon="photo-o" text="浏览历史"  @click="$router.push('myartical/history')">
        <i slot="icon" class="iconfont icon-liulanjilu"></i>
      </van-grid-item>
      <van-grid-item icon="photo-o" text="作品"  @click="$router.push('myartical')">
        <i slot="icon" class="iconfont icon-xiezuo"></i>
      </van-grid-item>
    </van-grid>
    <!-- 单元格 -->
    <div class="dyg">
      <van-cell title="消息通知" is-link />
      <van-cell title="小志同学" is-link  @click="$router.push('xiaozhi')"/>
    </div>
    <!-- 退出登录 -->

    <van-button block @click="tuichu" class="bg" v-if="$store.state.token">退出登录</van-button>
  </div>
</template>

<script>
import { userinfo } from '@/api/user'
export default {
  data () {
    return {
      user: {},
      imglist: {
        tu1: require('../../assets/img/mobile.png')
      }
    }
  },
  methods: {
    tuichu () {
      this.$dialog
        .confirm({
          title: '退出确认',
          message: '退出当前头条账号，将不能同步收藏，发布评论和云端分享等'
        })
        .then(() => {
          // on confirm
          this.$store.commit('addtoken', null)
        })
        .catch(() => {
          // on cancel
        })
    }
  },
  async created () {
    if (this.$store.state.token) {
      userinfo().then(res => {
        console.log(res)
        this.user = res.data
      })
    }
  }
}
</script>

<style lang='less' scoped>
i{
  font-size: 20px;
}
.bg {
  background: #fff;
  margin-top: 10px;
  color: #000;
}
.user {
  width: 100%;
  padding: 0 20px;
}
.xia {
  padding-top: 20px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: #fff;
  font-size: 14px;
  .item {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
}
.shang {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.zuo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  span {
    color: #fff;

    font-size: 16px;
    margin-left: 20px;
  }
}
.dyg {
  padding-top: 10px;
}
.top {
  height: 182px;
  background-image: url("../../assets/img/banner.png");
  background-size: 100% 182px;
  display: flex;
  align-items: center;
  justify-content: center;
  .denglu {
    display: flex;
    flex-direction: column;
    align-items: center;
    span {
      margin-top: 10px;
      color: #fff;
      font-size: 14px;
    }
  }
}
</style>
